import React from "react"
import SocialLinks from "../constants/socialLinks"
const Footer = () => {
  return (
  <footer className="footer">
    <div>
      <SocialLinks styleClass="footer-links"></SocialLinks>
        <p className="white-small">Template by <a className="white-small" href="https://github.com/john-smilga/starter-project-gatsby-strapi-portfolio-2020">John Smilga </a></p> 
    </div>
  </footer>
    )
}

export default Footer
