import React from "react"
import {
  FaFacebookSquare,
  FaLinkedin,
  FaWhatsappSquare,
  FaMeetup,
  FaMedium,
} from "react-icons/fa"

import {
BsFillPersonLinesFill
} from "react-icons/bs"

const data = [
  {
    id: 1,
    icon: <FaLinkedin className="social-icon"></FaLinkedin>,
    url: "https://www.linkedin.com/in/natanael-mota-b15b653a/",
  },
  {
    id: 2,
    icon: <FaMedium className="social-icon"></FaMedium>,
    url: "https://medium.com/@nat.c.mota",
  },
  {
    id: 3,
    icon: <BsFillPersonLinesFill className="social-icon"></BsFillPersonLinesFill>,
    url: "https://github.com/NatMota/natmota.github.io/blob/master/Natanael%20-%20CV.docx.pdf",
  },
  {
    id: 4,
    icon: <FaWhatsappSquare className="social-icon"></FaWhatsappSquare>,
    url: "https://wa.me/00447946849227",
  },
  {
    id: 5,
    icon: <FaMeetup className="social-icon"></FaMeetup>,
    url: "https://www.meetup.com/members/243265736/",
  }
]
const links = data.map(link => {
  return (
    <li key={link.id}>
      <a href={link.url} className="social-link">
        {link.icon}
      </a>
    </li>
  )
})

export default ({ styleClass }) => {
  return (
    <ul className={`social-links ${styleClass ? styleClass : ""}`}>{links}</ul>
  )
}
